/** THEME DEFAULTS
 * These values are used as fallbacks if Custom Values are not set.
 * Defaults are used by javascript to set fallbacks when custom value config
 * options are improperly set. This is required since some browsers (i.e. Firefox)
 * do not properly use @property initial-value when a variable is set to an
 * invalid value.
 **/

$defaults: (
  primary-color: #079561,
  secondary-color: #56D296,

  text-headline-color: #000,
  text-content-color: #859389,
  text-bold-color: #000,
  text-italic-color: #859389,
  text-underlined-color: #859389,
  text-link-color: #188bf6,
  text-highlight-color: #079561,
  text-dark-bg-color: #fff,

  icon-color: #079561,

  button-primary-text-color: #fff,
  button-primary-bg-color: #079561,
  button-secondary-text-color: #000,
  button-secondary-bg-color: #fff,

  background-1-color: #fff,
  background-2-color: #FBFBFB,
  background-3-color: #191E1C,

  headlinefont: 'Plus Jakarta Sans',
  contentfont: 'Plus Jakarta Sans',
);

@function get($var) {
  @return map-get($defaults, $var);
}

// Set --default-theme vars on root
:root {
  @each $name, $value in $defaults {
    --default-#{$name}: #{$value};
  }
}